const buttons = document.querySelectorAll('.synth-button');
const allah = document.querySelector('audio#allah');
const sickoMode = document.querySelector('audio#sicko-mode');
const happyMerchant = document.querySelector('audio#happy-merchant');
const mao = document.querySelector('audio#mao');
const meow = document.querySelector('audio#meow');

happyMerchant.volume = 0;
sickoMode.volume = 1;
mao.volume = 0;

let audioList = [sickoMode, happyMerchant, mao];
audioList.forEach((element) => {
  element.preservesPitch = 0;
});

const initializeButtons = (speed = 1.0) => {
  document.querySelector('#play').addEventListener('click', () => {
    if (sickoMode.paused) {
      audioList.forEach((element) => {
        element.play();
      });
    }
  });

  document.querySelector('#stop').addEventListener('click', () => {
    audioList.forEach((element) => {
      element.currentTime = 0;
      element.pause();
    });
  });

  document.querySelector('#pause').addEventListener('click', () => {
    if (!sickoMode.paused) {
      audioList.forEach((element) => {
        element.pause();
      });
    }
  });

  document.querySelector('#stolen').addEventListener('click', () => {
    alert('COŚ SIĘ ZJEBAŁO');
    allah.play();
  });
};

const setPlayBackSpeed = (speed) => {
  audioList.forEach((element) => {
    element.playbackRate = speed;
  });
};

const setMix = (mix) => {
  if (mix >= 0) {
    sickoMode.volume = 1 - mix;
    mao.volume = 0;
    happyMerchant.volume = mix;
  } else {
    console.log(mix + 1);
    sickoMode.volume = Number(mix) + 1;
    mao.volume = -Number(mix);
    happyMerchant.volume = 0;
  }
};

document.querySelector('.cat-image').addEventListener('click', () => {
  meow.play();
});

export { initializeButtons, setPlayBackSpeed, setMix };
