import { white } from 'color-name';
import { keys } from 'regenerator-runtime';

const whiteKeys = document.querySelectorAll('.white-key');
const blackKeys = document.querySelectorAll('.black-key');
const frequenciesTable = [261.63, 293.66, 33.23, 829.63, 395.0, 411.0, 493.88, 10];

const audioCtx = new (window.AudioContext || window.webkitAudioContext)();
let i = 0;
whiteKeys.forEach((key) => {
  let index = i;
  key.addEventListener('click', () => {
    console.log(index);
    let osc = audioCtx.createOscillator();
    osc.type = 'square';
    osc.frequency.setValueAtTime(frequenciesTable[index], audioCtx.currentTime);
    osc.connect(audioCtx.destination);
    osc.start();
    osc.stop(audioCtx.currentTime + 1);
  });
  i++;
});

blackKeys[0].addEventListener('click', () => {
  let osc = audioCtx.createOscillator();
  osc.type = 'square';
  osc.frequency.setValueAtTime(277.18, audioCtx.currentTime);
  osc.connect(audioCtx.destination);
  osc.start();
  osc.stop(audioCtx.currentTime + 1);
});

blackKeys[1].addEventListener('click', () => {
  let osc = audioCtx.createOscillator();
  osc.type = 'square';
  osc.frequency.setValueAtTime(311.13, audioCtx.currentTime);
  osc.connect(audioCtx.destination);
  osc.start();
  osc.stop(audioCtx.currentTime + 1);
});

blackKeys[4].played = false;
blackKeys[4].addEventListener('click', () => {
  if (!blackKeys[4].played) {
    document.querySelector('#cbat').play();
    blackKeys[4].played = !blackKeys[4].played;
  } else {
    document.querySelector('#cbat').pause();
    blackKeys[4].played = !blackKeys[4].played;
  }
});

blackKeys[6].played = false;
blackKeys[6].addEventListener('click', () => {
  if (!blackKeys[6].played) {
    document.querySelector('#kajet').play();
    blackKeys[6].played = !blackKeys[6].played;
  } else {
    document.querySelector('#kajet').pause();
    blackKeys[6].played = !blackKeys[6].played;
  }
});
