import { setPlayBackSpeed, setMix } from './buttons-app';

const speedSlider = document.querySelector('.slider#yellow');
const mixSlider = document.querySelector('.slider#red');

speedSlider.addEventListener('input', () => {
  setPlayBackSpeed(speedSlider.value);
});

mixSlider.addEventListener('input', () => {
  setMix(mixSlider.value);
});
